<template>
  <div class="hello">
<div class="container">Lates URL shortner</div>
<hr>
<label for="new-url">Paste your url here to get SHorty</label>
<br>
<input type="text" id="new-url" v-model="url" placeholder="Your Link here">
<br>
<label for="new-url">Paste your Slug here</label>
<br>
<input type="text" id="new-slug" v-model="slug" placeholder="(e.g:GGWP2020)">
<button v-on:click="createPost">Short url now!</button>
<p class="error" v-if="error">{{error}}</p>
<div class="posts-container">
<div class="post"   v-for="(post, index) in posts" 
v-bind:item="post" 
v-bind:index="index"
 v-bind:key="post._id"
 v-on:dblclick="deletePost(post._id)">
  
<p class="text">{{post.url}}</p>
<p class="text">{{post.slug}}</p>
<a :href="post.result">{{post.result}}</a>

</div>


</div>
  </div>
</template>

<script>
import PostService from '../services/PostService'

export default {
  name: 'IndexComponent',
 data(){
   return {
     posts: [],
     error: '',
     url: '',
     slug: '',
     result: ''
   }
 },
 async created(){
   try {
     this.posts = await PostService.getPosts()
   } catch (err) {
     this.error = err.message
   }
 },
 methods: {
   async createPost(){
     await PostService.insertPost(this.url,this.slug);
     this.posts = await PostService.getPosts();
   },
   async deletePost(id){
     await PostService.deletePost(id)
     this.posts = await PostService.getPosts()
   }
 },
}
</script>

